import { useState, useEffect } from 'react';
import { ReactComponent as Atom } from '../assets/Atom.svg';

const { REACT_APP_NEWS_KEY: API_KEY } = process.env;

/* -------------------------------------------------------------------------- */

function App() {
  // APIÎ°úÎ∂ÄÌÑ∞ ÎπÑÎèôÍ∏∞ ÏöîÏ≤≠ÏúºÎ°ú ÏùëÎãµ Î∞õÏùÑ Îç∞Ïù¥ÌÑ∞Î•º Î≥¥Í¥ÄÌï† ÏÉÅÌÉú
  const [news, setNews] = useState([]);
  // ÏóêÎü¨ ÏÉÅÌÉú
  const [hasError, setHasError] = useState(null);
  // Î°úÎî© ÏÉÅÌÉú Í¥ÄÎ¶¨
  const [isLoading, setIsLoading] = useState(true);

  // side effect
  // Î™ÖÎ†πÌòï, Íµ¨ÎèÖ(Ï∑®ÏÜå), ÎπÑÎèôÍ∏∞(ÎÑ§Ìä∏ÏõåÌÅ¨) Îç∞Ïù¥ÌÑ∞ Ìå®Ïπò, ...
  useEffect(
    () => {
      // ÎπÑÎèôÍ∏∞ ÏöîÏ≤≠
      fetch(
        `https://newsapi.org/v2/top-headlines?country=kr&category=technology&apiKey=${API_KEY}`
      )
      .then(response => response.json()) // resolved
      .then(res => {
        console.log(res);
        setNews(res.articles);
        setIsLoading(false);
      }) // ÌïÑÏöîÌïú Îç∞Ïù¥ÌÑ∞Îßå ÎîîÏä§Ìä∏Îü≠Ï≤òÎßÅ Ìï†ÎãπÏúºÎ°ú ÎΩëÏïÑÎÇ¥Ïñ¥ Ï≤òÎ¶¨ÌïòÍ∏∞
      .catch(
        // rejected
        (error) => {
          setHasError(error);
        }
      );
    }, [] // ÎßàÏö¥Ìä∏(=ÏµúÏ¥à Î†åÎçîÎßÅ) ÎïåÎßå Ïã§Ìñâ -> Îî± 1ÌöåÎßå Ïã§Ìñâ!
  );
  // useEffectÎäî Ìï®ÏàòÎ•º Î∞òÌôòÌïòÎäîÎç∞, Ïù¥Î•º clean up Ìï®ÏàòÎùºÍ≥† Ìï®.
  // "Îí∑Ï≤òÎ¶¨ Ìï®Ïàò" üôÇ -> Î©îÎ™®Î¶¨ ÎÇ≠ÎπÑÎ•º Î∞©ÏßÄÌïòÍ∏∞ ÏúÑÌïú!
  // ex) remove event listener

  if (isLoading) {
    return <Atom />
  }

  if (hasError) { // ÏóêÎü¨Í∞Ä Î∞úÏÉùÌïú ÏÉÅÌô©ÏóêÏÑú Î†åÎçîÎßÅ
    return <div role="alert">{hasError.message}</div>
  }

  // Ï†ïÏÉÅÏ†ÅÏúºÎ°ú ÏöîÏ≤≠Ïù¥ Ï≤òÎ¶¨ÎêòÏñ¥ ÏùëÎãµÏù¥ Ïò® Í≤ΩÏö∞ Î†åÎçîÎßÅ
  return (
    <div className="App">
      <h1>World Wide News Headlines</h1>
      <div className="newsArea" lang="ko">
        <ul>
          { // to render list, use map()
            // each child in list MUST have their own unique key
            news.map(item => {
              return (
                <li key={item.url}>
                  {/* {JSON.stringify(item, null, 2)} */}
                  <img src={item.urlToImage} alt="" />
                </li>
              )
            })
          }
        </ul>
      </div>
    </div>
  )
}

export default App
